<script>
import api from "@/api"
import MenuFirst from '@/components/MenuFirst.vue'
import MenuSecond from '@/components/MenuSecond.vue'
import { getToken } from '@/api/auth'

export default {
	inject: ["reload"],
	data() {
		return {
			contentW: this.$store.state.contentW,
			isfold: true,
			userInfo: {}
		}
	},
	mounted: function() {
		const token = getToken();
		api.headNumMsg({"token": token}).then(res => {
			// console.log(res)
			const data = res.data;
			if(res.code === 0){
				this.userInfo = data
				this.$store.commit('SET_USERINFO', data)
			} else {
				this.$Notice.error({title:res.msg})
			}
		})
	},
	methods: {
		toggleClick () {
			this.$store.commit('SET_MENUSECONDW');
			this.contentW = this.$store.state.contentW;
			this.contentW === 20 ? this.isfold = true : this.isfold = false;
		}
	},
	reloadPage () {
		this.reload();
	},
	components: {MenuFirst,MenuSecond,}
}
</script>
<template>
    <div class="layout">
			<Menu mode="horizontal" theme="dark" active-name="1">
				<router-link :to="{path:'/admin/index'}">
					<div class="layout-logo"></div>
					<div class="projectName">BMS后台管理系统</div>
				</router-link>
				<div class="head_welcome">欢迎登录: [<b> {{userInfo.username}} </b>] 感谢您奔腾不息的工作!   您今日已经完成 <b>{{userInfo.originalCount+userInfo.originalCounta}}</b> 篇优质原创 和 <b>{{(userInfo.nOriginalCount+userInfo.nOriginalCounta)}}</b> 篇内容更新！</div>
			</Menu>
			
			<MenuFirst />
			
			<div class="foldRobot" :class="{onfold: isfold}" @click="toggleClick" trigger="hover">
				<svg-icon icon-class="robotFold" width=48 height=48></svg-icon>
			</div>
			
			<div class="layout-content">
				<Row>
					<MenuSecond />
					
					<i-col :span="contentW" class="ivu-minheight contentR">
						<keep-alive>
							<router-view v-if="this.$route.meta.keepAlive"></router-view>
						</keep-alive> 
						<router-view v-if="!this.$route.meta.keepAlive"></router-view>
					</i-col>
				</Row>
			</div>
			<div class="layout-copy">
					Copyright © 2019 BMS.com Inc. All Rights Reserved 上海BMS网络科技股份有限公司
			</div>
    </div>
</template>
