<script>
import api from '@/api'
import { getToken,delToken } from '@/api/auth'

export default {
  inject: ['reload'],
  name: 'menufirst',
  data() {
    return {
      menuData: [],
      iconFirstList: ["logo-angular","ios-cog","ios-analytics","ios-ionitron-outline","ios-construct-outline"],
      curAdress: ''
    }
  },
  mounted() {
    this.menuData = this.$store.state.menuFirst;
    this.curAdress = this.$route.path.split('/')[1];
  },
  methods: {
    isCurMenu: function(path) {
      if(this.$route.path === '/') {
        const falg = path.replace(/\//gi, '') === 'adminindex' ? true : false;
        return falg;
      } else {
        const curRoute = this.$route.path.replace(/\//gi, '');
        path = path.replace(/\//gi, '');
        // console.log(curRoute,path )
        
        return curRoute === path;
      }
    },
    // setPath: function(path) {
    //   this.$store.commit('SET_PATH', '/'+path)
    //   // console.log(this.$store.state.defaultPath)
    // },
    logout: function() {
      const token = getToken();
      api.logout({'token': token}).then(res => {
        if(res.code === 0) {
          delToken();
          this.$Notice.success({title: res.msg})
          this.$router.push({path: '/login'})
        } else {
          this.$Notice.error({title: res.msg})
        }
      }).catch(err => {
        this.$Notice.error(err)
      })
    }
  },
  watch: {
    $route: function(to) {
      this.curAdress = to.path.split('/')[1];
    }
  },
}
</script>
<template>
  <div id="menu_first">
    <Menu mode="horizontal" :active-name="curAdress">
      <div class="layout-assistant menu_l1">
        <router-link v-for="itm, index in menuData" :to="{path:'/'+itm.marks}"  :key="index">
          <Menu-item
            :class="{'ivu-menu-item-active': isCurMenu(itm.href)}"
            :name="itm.marks"
          >
            <Icon :type="iconFirstList[index]" size="28" />
            {{itm.name}}
          </Menu-item>
        </router-link>
      </div>
      
      <Dropdown class="loginDrop">
        <div class="admin"></div>
        <Dropdown-menu slot="list">
            <router-link :to="{path:'/admin/upPwd'}">
              <Dropdown-item>修改密码</Dropdown-item>
            </router-link>
            <Dropdown-item @click.native="logout()">退出</Dropdown-item>
        </Dropdown-menu>
      </Dropdown>
    </Menu>
  </div>
</template>
<style lang="scss" scoped>
  .ivu-menu-item {
    a {
      display: block;
      font-size: 16px;
      color: #515a6e;
    }
    &:hover a {
      color: #2d8cf0;
    }
  }
  .ivu-menu-item-active {
    a {
      color: #2d8cf0;
    }
  }
  .layout-assistant{
		display: flex;
		align-content: center;
		justify-content: center;
	}
  .loginDrop {
		position: absolute;
    top: 0;
    right: 40px;
    .admin {
      width: 60px;
      height: 50px;
      border-radius: 10%;
      margin-top: 5px;
      background: url(~assets/index_images/face1.gif) no-repeat center;
      background-size: cover;
    }
    &:hover .admin {
      background: url(~assets/index_images/face2.gif) no-repeat center;
      background-size: cover;
    }
	}
</style>