<script>
export default {
  name: 'menusecond',
  inject: ['reload'],
  data() {
    return {
      menuSecondW: this.$store.state.menuSecondW,
      isfold: false,
      menuData: this.$store.state.menuSecond || [],
      menuIcon: ["ios-sunny-outline","ios-partly-sunny-outline","ios-cloudy-outline","ios-rainy-outline","ios-cloudy-night-outline"],
      // defaultPath: '',
      curFirstPath: '',
      curSecondPath: '',
      openArr: []
    }
  },
  created () {
    this.isfold = this.menuSecondW === 4 ?  true : false;
    // this.defaultPath = this.$route.path.split('/')[1];
    this.curFirstPath = this.$route.path.split('/')[1];
    this.curSecondPath = this.$route.path.split('/')[2];
    this.setOpenArr();
  },
  computed: {
    iconSize () {
      return this.menuSecondW === 2 ? 28 : 24;
    },
    getMenuSecondW () {
      return this.$store.state.menuSecondW;
    },
    getMenuSecond () {
      return this.$store.state.menuSecond;
    }
  },
  watch: {
    getMenuSecondW: function() {
      this.menuSecondW = this.$store.state.menuSecondW;
      this.isfold = this.menuSecondW === 4 ?  true : false;
    },
    getMenuSecond: function() {
      this.menuData = this.$store.state.menuSecond;
    },
    $route: function(to) {
      this.curFirstPath = to.path.split('/')[1];
      this.curSecondPath = to.path.split('/')[2];
      // console.log(this.curFirstPath, this.curSecondPath)
    }
  },
  methods: {
    setOpenArr () {
      this.openArr = [];
      this.menuData.forEach((ele) => {
        this.openArr.push(ele.id)
      })
    }
  }
}
</script>
<template>
  <i-col :span="menuSecondW">
    <Menu :active-name="curSecondPath"  class="ivu-minheight ivu-col" width="auto" :open-names="openArr">
      <Submenu v-for="itm,index1 in menuData" :name="itm.id" :key="index1">
        <template slot="title">
          <Icon :type="menuIcon[index1]" :size="iconSize" />
          <span v-show="isfold"	class="layout-text">{{itm.name}}</span>
        </template>
        <template v-for="item,index2 in itm.third">
          <router-link :to="{path:'/'+curFirstPath+'/'+item.marks}" :key="index2">
            <Menu-item :name="item.marks">
              {{item.name}}
            </Menu-item>
          </router-link>
        </template>
      </Submenu>
    </Menu>
  </i-col>
</template>
<style lang="scss" scoped>
  .ivu-minheight {
		min-height: 740px;
	}
  .ivu-menu-submenu-title {
    .layout-text{
      font-size: 15px;
    }
    .ivu-icon {
      position: relative;
      top: -2px;
    }
  }
  .ivu-menu {
    a {
      display: block;
      font-size: 14px;
      color: #515a6e;
      &:hover a {
        color: #2d8cf0;
      }
    }
  }
  .ivu-menu-item-selected {
    a {
      color: #2d8cf0;
    }
  }
</style>